<template>
  <div>
    <h4 class="text-center">게시글 내용</h4>
    <table class="table table-condensed w-100" v-if="board">
      <tr>
        <th>번호</th>
        <td v-text="board.no"></td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td v-text="board.writer"></td>
      </tr>
      <tr>
        <th>제목</th>
        <td v-text="board.title"></td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ board.regtime | toDate }}</td>
      </tr>
      <tr>
        <th>내용</th>
        <td colspan="2" v-text="board.content"></td>
      </tr>
    </table>

    <div v-if="board.comment != null">
      <h4>댓글</h4>
      <div v-text="board.comment"></div>
    </div>

    <div v-if="board.comment == null">
      <h4>댓글 등록</h4>
      <textarea id="comment" v-model.lazy="board.comment" ref="comment" cols="50" rows="5" ></textarea>
      <button class="btn btn-primary" @click="updateHandler">등록</button>
    </div>


    <div class="text-right">
      <router-link class="btn btn-primary" to="/list">목록</router-link>
      <router-link class="btn btn-primary" :to="'/update?no=' + board.no">수정</router-link>
      <router-link class="btn btn-primary" :to="'/remove?no=' + board.no">삭제</router-link>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import moment from "moment";
import {mapGetters} from "vuex";

export default {
   computed:{
    ...mapGetters(["board"])
  },
  filters: {
    toDate: function (regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD");
    }
  },
  created() {
    this.$store.dispatch('getBoard', `/board/${this.$route.query.no}`)
  },
  methods:{
    updateHandler: function() {
      //데이타 검증
      let err = true;
      let msg = "";

      !this.comment && ((msg = "댓글을 입력해 주세요"), (err = false), this.$refs.comment.focus());

      if (!err)
        alert(msg);
      else {
        console.log("............update:comment");
        axios
          .put(`http://localhost:9999/vue/api/board`, {
            no: this.board.no,
            title: this.board.title,
            writer: this.board.writer,
            content: this.board.content,
            comment: this.board.comment
          })
          .then(({ data }) => {
            if (data == "success") {
              console.log("update................", data);
              alert("수정 완료!!!");
              this.moveHandler();
            } else {
              alert("수정 중 오류 발생");
            }
          })
          .catch(() => {
            alert("오류 발생");
          });
      }
    },
     moveHandler: function() {
       console.log("comment : "+this.board);
      this.$router.push("'search?no=' + board.no");
    }

  }
};
</script>